}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 1100px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media screen and (max-width: 767px) {
  html {
    font-size: 16px;
  }
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

li {
  list-style: none;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

textarea {
  resize: vertical;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

/* Blur images when they have no alt attribute */
/* フォームリセット */
.c-btn {
  inline-size: fit-content;
  outline-offset: 2px;
  touch-action: manipulation;
  user-select: none;
}
.c-btn:focus-visible {
  outline-offset: 2px;
}

@media screen and (max-width: 767px) {
  .c-hamburger {
    display: inline-block;
    text-decoration: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border: none;
    background: #666;
    cursor: pointer;
    -webkit-transition: all 0.3s ease-in-out;
    transition: background 0.3s ease;
    position: relative;
    width: 3.125rem;
    height: 3.125rem;
    border-radius: 50%;
  }
}

@media (hover: hover) {
  .c-hamburger:hover {
    background: #333;
  }
  .c-hamburger:hover .c-hamburger__line {
    background: #fff;
  }
  .c-hamburger:hover[aria-expanded=true] .c-hamburger__line {
    background: transparent;
  }
}
.c-hamburger__line {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.25rem;
  height: 1px;
  background: #fff;
  transition: background 0.3s ease;
}

.c-hamburger__line::before,
.c-hamburger__line::after {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: inherit;
  content: "";
  transition: transform 0.3s ease;
}

.c-hamburger__line::before {
  top: -0.375rem;
}

.c-hamburger__line::after {
  top: 0.375rem;
}

.c-hamburger[aria-expanded=true] {
  background: #333;
}
.c-hamburger[aria-expanded=true] .c-hamburger__line {
  background: transparent;
}
.c-hamburger[aria-expanded=true] .c-hamburger__line::before, .c-hamburger[aria-expanded=true] .c-hamburger__line::after {
  top: 0;
  background: #fff;
}
.c-hamburger[aria-expanded=true] .c-hamburger__line::before {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.c-hamburger[aria-expanded=true] .c-hamburger__line::after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.c-page-top {
  width: 2.8125rem;
  height: 2.8125rem;
  background: #fff;
  position: fixed;
  bottom: 3.125rem;
  right: 1.25rem;
  transition: 0.3s;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 99;
}

.c-page-top.is-active {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

.c-page-top span:first-child {
  display: inline-block;
  width: 1.25rem;
  height: 0.125rem;
  background: #333;
  transform: rotate(-45deg) translateX(3px);
  transform-origin: right;
  transition: 0.3s;
}

.c-page-top span:nth-child(2) {
  display: inline-block;
  width: 1.25rem;
  height: 0.125rem;
  background: #333;
  transform: rotate(45deg) translateX(-3px);
  transform-origin: left;
  transition: 0.3s;
}

.c-page-top:hover {
  background: #333;
}
.c-page-top:hover span {
  background: #fff;
}

.pagination {
  display: inline-block;
}
.pagination .nav-links {
  display: flex;
  gap: 0.9375rem;
}
@media screen and (max-width: 767px) {
  .pagination .nav-links {
    gap: 0.625rem;
  }
}
.pagination .page-numbers {
  display: grid;
  place-content: center;
  padding: 0.625rem;
  width: 5rem;
  aspect-ratio: 1;
  border-radius: 50%;
  color: #666;
  background: #fff;
  border: 1px solid #ccc;
  font-size: 1rem;
  font-weight: 700;
  transition: all 0.3s ease;
}
@media screen and (max-width: 767px) {
  .pagination .page-numbers {
    width: 2.1875rem;
    padding: 0.3125rem;
    font-size: 0.75rem;
  }
}
@media (hover: hover) {
  .pagination .page-numbers:hover {
    background: #666;
    color: #fff;
  }
}
.pagination .current {
  background: #666;
  color: #fff;
}
.pagination .prev,
.pagination .next {
  position: relative;
}
.pagination .prev::after,
.pagination .next::after {
  content: "";
  display: block;
  width: 1.375rem;
  height: 1rem;
  position: absolute;
  inset: 0;
  margin: auto;
}
@media screen and (max-width: 767px) {
  .pagination .prev::after,
  .pagination .next::after {
    width: 0.9375rem;
    height: 0.6875rem;
  }
}
.pagination .prev {
  transform: rotate(180deg);
}
.pagination .dots {
  background: transparent;
  box-shadow: none;
}

