    height: calc(100% - 5rem);
    overflow-y: scroll;
    z-index: 102;
    background: #ccc;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: opacity 0.3s ease, visibility 0.3s ease, background 0.3s ease;
